		var htm = '<div class="am-modal am-modal-no-btn" tabindex="-1" id="dialog-tips">' + '<div class="am-modal-dialog dialog-modal">' + '<div class="am-modal-bd">' + txt + '</div>' + '</div>' + '</div>';
